
/* The whole page shared element */

.app-container {
    background-color: rgba(152, 215, 228, 0.6);
}

/* All pages shared the same style of nav bar */

nav {
    width: 100%;
    background-color: rgba(253, 250, 180, 0.6);
    display: flex;
    justify-content: space-around;
    align-items: center;
    .nav-links {
        list-style: none;
        display: flex;
        .nav-icon a {
            display: inline-block;
            text-decoration: none;
            color: rgb(0, 0, 0);
            padding: 1em;
            margin-right: 2em;
            &:hover {
                color: rgb(235, 101, 130);
                font-weight: bold;
            }
        }
    }
}

nav.vertical {
    width: 150px;
    height: 100vh;
    flex-direction: row;
    position: absolute;
    left: 0;
    top: 0;
    .nav-links {
        flex-direction: column;
        .nav-item a {
            display: block;
            padding: 4em 0;
            
        }
    }

}

/* title and subtitle element style */

#title {
    font-size: 40px;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
    color:rgb(44, 43, 43);
    margin:auto;
    margin-top: 50px;
	width: 1050px;
	display: flex;
	border-radius: 5px;
}

#subtitle {
    font-size: 20px;
    font-family: Arial, Helvetica, sans-serif;
    color:rgb(44, 43, 43);
    margin:auto;
	width: 1050px;
	display: flex;
}

/* Pomodoro timer elements style and color setting */

  .timer {
    font-family: sans-serif;
    display: inline-block;
    padding: 24px 32px;
    margin: 50px auto 40px 740px;
    border-radius: 40px;
    background: #e7e6e6f6;
  }
  
  .timer__part {
    font-size: 60px;
    font-weight: bold;
  }
  
  .timer__btn {
    width: 60px;
    height: 60px;
    margin-left: 16px;
    border-radius: 50%;
    border: none;
    color: white;
    background: #8208e6;
    cursor: pointer;
  }
  
  .timer__btn--start {
    background: #00b84c;
  }
  
  .timer__btn--stop {
    background: #ff0256;
  }
  